@import url(https://fonts.googleapis.com/css2?family=Gothic+A1:wght@400;700&display=swap);
.header{width:35%;height:100vh;position:fixed;display:flex;flex-direction:column}.header nav{display:flex;flex-direction:column}.header nav li{margin-bottom:1.5rem;color:#858586;font-size:12px;font-weight:bold}.header nav li a{text-decoration:none;color:#858586;letter-spacing:1.5px;text-transform:uppercase;transition:color 0.2s ease-in-out}.header nav li .line{display:inline-block;min-width:36px;min-height:1px;background-color:#858586;position:relative;top:-5px;margin:0 0.5rem;transition:background-color 0.2s ease-in-out, min-width 0.5s}.header nav li .number{font-size:11px;transition:color 0.2s ease-in-out}.header nav li:hover .line,.header nav li:active .line{background-color:#fff;min-width:54px}.header nav li:hover a,.header nav li:active a{color:white}.header nav li:hover .number,.header nav li:active .number{color:white}p{line-height:200%;font-size:14px;color:#949495}h1{font-size:50px}.mb5{margin-bottom:5rem}.skill{color:white;text-decoration:underline}i{font-size:14px}.social{margin-top:auto;margin-bottom:3rem;display:flex;justify-content:space-between}.social img{width:52px;height:52px;border-radius:50%;border:black 2px solid}.social span{font-size:14px;align-self:center}.social span span{margin:0 12px}

.skills{display:flex;justify-content:space-between;width:100%;align-self:center;padding-top:2rem;padding-bottom:3rem}.skills i{font-size:48px}.skills img{position:relative;top:2px}@media (max-width: 900px){.header,.body{width:100% !important;display:block;position:relative !important;margin:0 auto;height:100%;right:0 !important;padding-top:0 !important}.social{justify-content:flex-start}.social>*{margin-top:2rem;margin-right:2rem}.skills{justify-content:space-evenly}.container{padding:1rem !important}nav{display:none !important}}@media (max-width: 1050px){h1{margin-bottom:0;font-size:36px}}

.body{width:50%;position:absolute;top:0;right:3rem;padding-top:17px}.body h3{text-align:center;font-size:34px}.body h3:not(:first-of-type){margin:3rem auto}

.project-tab{background-color:#202022;display:flex;flex-direction:column;justify-content:space-evenly;padding:1rem 3rem 2rem;border-radius:0.5rem;transition:transform 0.2s ease-in-out, box-shadow 0.3s ease-in-out;position:relative;margin:1.5rem auto;max-width:650px;z-index:100}.project-tab h2,.project-tab h5{letter-spacing:4px}.project-tab h5{margin-bottom:0.5rem}.project-tab h2{margin-top:0;font-size:30px;margin-bottom:0.8rem}.project-tab p{margin-top:0}.project-tab:hover{transform:scale(1.1);box-shadow:0px 0px 5px 1px rgba(255,255,255,0.8)}@media (max-width: 1024px){.project-tab{max-width:450px}}@media (max-width: 900px){.project-tab{max-width:90%}}a{text-decoration:none;color:#fff}

*{box-sizing:border-box}html{font-family:Gothic A1;font-weight:500;background-color:#151515;color:whitesmoke}html,body,ul{margin:0;padding:0;list-style-type:none}.container{padding:1rem 0rem 1rem 4rem}

